h1, h2, h3, h4, h5, h6 {
   margin: 0;
}

.app-wrapper {
   max-width: 1200px;
   margin: 0 auto;
   display: grid;
   grid-template-areas: 
   "h h"
   "n c"
   ;
   grid-template-rows: 60px 1fr;
   grid-template-columns: 2fr 10fr;
   font-family: 'Nunito Sans', sans-serif;
}

.app-wrapper-content {
   grid-area: c;
   background-color: rgb(252, 251, 227);
}